import React from 'react';
import store from 'store';
import { Icon } from 'react-icons-kit';
import { smallDown } from 'react-icons-kit/entypo/smallDown'

import './mobileSelectCity.scss';

const MobileSelectCity = (props) => {

    const [ selectOpened, setSelectOpened ] = React.useState(false);

    const cities = [
        {
            label: 'Днепр',
            id: 1
        },
        {
            label: 'Запорожье',
            id: 2
        },
        {
            label: 'Харьков',
            id: 3
        }
    ];

    const onSetSelect = () => {
        setSelectOpened(!selectOpened);
    };

    const onChangeCityHandler = (id) => {
        setSelectOpened(!selectOpened);
        props.setCity(id);
        const storage = store.get('24rolls');
        if (id === 1) {
            storage.customOptions.city = 'Dnipro';
        } else if (id === 2) {
            storage.customOptions.city = 'Zaporijja';
        } else {
            storage.customOptions.city = 'Kharkov';
        }
        store.set('24rolls', storage);
        props.onSetBaseName(id === 1 ? '/dp' : id === 2 ? '/zp' : '/kh');
        props.history.push('/');
    }

    return (
        <div className="mobile-selectcity-container">
            <h1 onClick={onSetSelect}>
                {'Ваш город: '}
                <span>
                    {cities[props.city - 1].label}
                </span> 
                <i>
                    <Icon 
                    size={'100%'}
                    icon={smallDown}
                    />
                </i>
            </h1>
            {selectOpened && <div className="mobile-selectcity-container__list">
                <p>{'Выберите ваш город:'}</p>
                {cities.map(el => (
                    <span
                    key={el.id}
                    onClick={() => onChangeCityHandler(el.id)}
                    >
                        {el.label}
                    </span>
                ))}
            </div>}
        </div>
    );
};

export default MobileSelectCity;